import { useEffect, useState } from "react";
import { Box, Button, useColorModeValue, Image } from "@chakra-ui/react";
import { Link as RouterLink, useLocation } from "react-router-dom";
import avatar from "./assets/avatar_withback.png";
import Hello from './assets/Hello.gif'

export const FloatingChatButton = () => {
  const location = useLocation();
  const [showBubble, setShowBubble] = useState(false);

  // ✅ 훅은 항상 최상단에서 호출
  const bubbleBg = useColorModeValue("white", "gray.700");
  const bubbleColor = useColorModeValue("gray.800", "white");
  const bubbleShadow = useColorModeValue("md", "dark-lg");

  useEffect(() => {
    // 0.8초 후 말풍선 등장
    const timer = setTimeout(() => setShowBubble(true), 800);
    return () => clearTimeout(timer);
  }, []);

  if (location.pathname === "/chat") return null;

  return (
    <Box position="fixed" bottom="15px" right="15px" zIndex="100">
      {/* 💬 말풍선 */}
      {showBubble && (
        <Box
        
          position="absolute"
          bottom="90px"       // 버튼과의 거리
          right="0"
          bg={bubbleBg}
          color={bubbleColor}
          px="4"
          py="2"
          borderRadius="lg"
          boxShadow={bubbleShadow}
          fontSize="sm"
          whiteSpace="nowrap"
          transition="opacity 0.4s ease, transform 0.4s ease"
          opacity={1}
          transform="translateY(0)"
          _before={{
            content: '""',
            position: "absolute",
            bottom: "-6px",
            right: "22px",
            width: "0",
            height: "0",
            borderLeft: "6px solid transparent",
            borderRight: "6px solid transparent",
            borderTop: `6px solid ${bubbleBg}`,
          }}
        >
            
      {/* <img src={Hello} alt="hello" width="20" height="20" display={{ base: "none", md: "block" }} /> */}
      <Image
      src={Hello}
      alt="hello"
      width="20px"
      height="20px"
      display={{ base: "none", md: "block" }}
    />
    
         Talk with my AI self 
         
        </Box>
      )}

      
      <Button
        as={RouterLink}
        to="/chat"
        variant="ghost"
        p={1}
        height="auto"
        minW="auto"
      
        _hover={{ transform: "scale(1.05)", bg: "transparent" }}  // hover 시 배경 없음
        _focus={{ boxShadow: "none", bg: "transparent" }}          // focus 시 배경 없음
        _active={{ bg: "transparent" }}     
        transition="transform 0.2s ease"
      >
        <img
          src={avatar}
          alt="chatbot"
          width="80"
          height="80"
          style={{ borderRadius: "50%" }}
        />
      </Button>
    </Box>
  );
};
